export default {
  data() {
    return {
      operationOptions: ['', ''],
      distributaryOrganOptions: [
        ['', 'ODR'],
      ],
      diversionDirectionOptions: [['', '', '']],
      feedbackPersonOptions: [['']],
      acceptDeptOptions: [['-']],
      deptTypeOptions: [['', '']],
      approverOptions: [['']],
      approvalDepartmentOptions: [['']],
      operation: '',
      initialInspectionDate: '',
      deadlineForProcessing: '',
      postscript: '',
      distributaryOrgan: [],
      approvalDepartment: [],
      acceptDept: [],
      deptType: [],
      approver: [],
      diversionDirection: [],
      //   
      trial: false,
      // 
      isOperation: true,
    };
  },
  methods: {
    change(value) {
      if (value === '') {
        this.trial = true;
      } else {
        this.trial = false;
      }
    },
    onConfirm() {},
    changeOrgan() {
      if (this.distributaryOrgan[0] === '') {
        this.diversionDirection[0] = '';
        console.log(this.diversionDirection);
      }
    },
    changeDistributary() {
      if (this.diversionDirection[0] === '') {
        this.isOperation = false;
      } else {
        this.isOperation = true;
      }
    },
  },
};



