import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as getters from './getters'

Vue.use(Vuex)

// 
const state = {
    unitList: [],
    user: undefined,
}

//  mutations
const mutations = {
    //
    UPDATEUNITLIST(state, payload) {
        state.unitList = payload
    },
    //
    UPDATEUSERINFO(state, payload) {
        state.user = payload
    },
}

//  store 
export default new Vuex.Store({
    actions,
    getters,
    state,
    mutations
})


